@import url(https://fonts.googleapis.com/css?family=Raleway:400,500&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#333;color:#333;font-family:'Raleway', sans-serif;font-weight:400;display:flex;justify-content:center;align-items:center;height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}*{margin:0;padding:0;list-style:none;vertical-align:baseline}

div{position:relative;z-index:2}.footer{width:450px;text-align:center;display:block;margin:15px auto 30px auto;font-size:0.8em;color:#fff}.footer a{font-weight:500;text-decoration:none;color:#fff}#quote-box{border-radius:3px;position:relative;width:450px;padding:40px 50px;display:table;background-color:#fff}#quote-box .quote-text{text-align:center;width:450px;height:auto;clear:both;font-weight:500;font-size:1.75em}#quote-box .quote-text i{font-size:1.0em;margin-right:0.4em}#quote-box .quote-author{width:450px;height:auto;clear:both;padding-top:20px;font-size:1em;text-align:right}#quote-box .buttons{width:450px;margin:auto;display:block}#quote-box .buttons .button{height:38px;border:none;border-radius:3px;color:#fff;background-color:#333;outline:none;font-size:0.85em;padding:8px 18px 6px 18px;margin-top:30px;opacity:1;cursor:pointer}#quote-box .buttons .button:hover{opacity:0.9}#quote-box .buttons .button#tweet-quote,#quote-box .buttons .button#tumblr-quote{float:left;padding:8px 0 0;text-align:center;font-size:1.2em;margin-right:5px;height:30px;width:40px}#quote-box .buttons .button#new-quote{float:right}

